import React from "react";
import Badge from "../Badge";
import ButtonBuy from "../UI/ButtonBuy";
import PropTypes from "prop-types";
import Icon from "../UI/Icon";
import Price from "../Price";
import Bookmark from "../Bookmark";
import "./card.scss";
import {Link} from "react-router-dom";
import Rating from "../Rating";
import {useSelector} from "react-redux";

const Card = ({ product, inGrid }) => {
  const massFavorites = useSelector(state => state.favoritesReducer.favorites);
  const { id, name, price, discount, oldPrice, badges, reviews, ratingProduct: rating } = product;
  const favorite = massFavorites.some(favorite => favorite.id === product.id);
  const reviewsNumber = reviews.length;
  return (
    <div className="w-full hover:shadow-2xl duration-300">
      <Link
        to={`${process.env.PUBLIC_URL}/product/${id}`}
        className="group relative pt-2 px-4 pb-4 rounded flex flex-col gap-3 max-w-[230px] card h-full"
      >
        <div className="min-w-[200px] relative pt-8">
          <div className="flex gap-2 flex-wrap pr-5 absolute top-0">
            {
              !!badges.length && badges.map(badge =>
                <Badge key={badge.id} text={badge.text} type={badge.name} />
              )
            }
          </div>
          <div className="h-[150px]">
            {id && (
              <img className="w-full h-full object-contain mx-auto" src={`https://thumb.cloud.mail.ru/weblink/thumb/xw1/9Q7k/wEByutoNc/${id}.jpg`} alt="" />
            )}
          </div>
        </div>
        <div className="card__info gap-1 h-full flex flex-col">
          <div className="flex items-center gap-3">
            {!!reviewsNumber && (
              <>
                <Rating rating={rating} />
                <div className="flex items-center gap-1">
                  <Icon name="review" className="text-gray-400 w-[16px] h-[16px]" />
                  <span>{reviewsNumber}</span>
                </div>
              </>
            )}
          </div>
          <div className="card__info-name line-clamp-3 hover:text-sky-500">{name}</div>
          <div className="card__info-description hidden text-sm">
            : 16; 19201200; IPS;<br/>
            : Intel Core i7 12700H 2.3  (4.7 ,   Turbo)<br/>
             : Intel Iris Xe graphics ;<br/>
              16 , LPDDR4x;<br/>
            : SSD 512 ;<br/>
             : Windows 11 Home;<br/>
            :   
          </div>
          <div className={"mt-auto flex flex-col gap-1" + (discount ? "" : " pt-4")}>
            <div className="card__info-bottom flex justify-between">
              <div className="card__info-price">
                <Price price={price} oldPrice={oldPrice} discount={discount} hover={true}/>
              </div>
              <div>
                <div className="card__bookmark absolute right-4 top-4">
                  <Bookmark status={favorite} product={product} />
                </div>
                <div className="card__button-buy absolute right-4 bottom-[56px]">
                  <ButtonBuy min={inGrid} product={product} />
                </div>
              </div>
            </div>
            <div className="text-sm">
              <div>  <span className="underline font-medium cursor-pointer hover:text-sky-500"> 20 </span></div>
              <div> <span className="underline font-medium cursor-pointer hover:text-sky-500"> 500 </span></div>
            </div>
          </div>
        </div>
      </Link>
    </div>
  );
};

Card.propTypes = {
  product: PropTypes.object.isRequired,
  // id: PropTypes.number,
  // name: PropTypes.string.isRequired,
  // price: PropTypes.number.isRequired,
  // oldPrice: PropTypes.number,
  // discount: PropTypes.number,
  // listBadges: PropTypes.arrayOf(
  //   PropTypes.shape({
  //     id: PropTypes.number.isRequired,
  //     text: PropTypes.string.isRequired,
  //     name: PropTypes.string.isRequired
  //   })
  // ),
  // reviewsNumber: PropTypes.number,
  // rating: PropTypes.number,
  inGrid: PropTypes.bool
};

export default Card;
