import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import api from "../../api";
import Price from "../../components/Price";
import ButtonBuy from "../../components/UI/ButtonBuy";
import Loader from "../../components/Loader";
import Review from "../../components/Review";
import paginate from "../../utils/paginate";
import Pagination from "../../components/Pagination";
import SortOptions from "../../components/SortOptions";
import { orderBy } from "lodash";

const ProductPage = () => {
  const { productId } = useParams();
  const [product, setProduct] = useState(null);
  const [currentPageReview, setCurrentPageReview] = useState(1);
  const [sortReviewBy, setSortReviewBy] = useState({ iter: "rating", order: "desc" });
  const reviewOnPage = 5;
  let reviews = [];
  if (product) reviews = product.reviews;

  const sortReviewsOptions = [
    { field: "rating", text: " " },
    { field: "date", text: " " }
  ];

  const handleSortReviews = (value) => {
    setSortReviewBy(value);
    setCurrentPageReview(1);
  };

  const handlePageReviewChange = (pageNumber) => {
    setCurrentPageReview(pageNumber);
  };

  const sortByDate = (items) => {
    return orderBy(items, [item => {
      const [day, month, year] = item.date.split(".");
      return new Date(`${year}-${month}-${day}`);
    }], [sortReviewBy.order]);
  };

  let sortedReviews = reviews;
  if (sortReviewBy) {
    sortedReviews = sortReviewBy.iter === "date"
      ? sortByDate(reviews)
      : orderBy(reviews, [sortReviewBy.iter], [sortReviewBy.order]);
  }
  const reviewsCrop = paginate(sortedReviews, reviewOnPage, currentPageReview);
  const showProduct = () => {
    if (product) {
      return (
        <div className="pt-8 pb-20 flex flex-col gap-20">
          <div>
            <h2 className="text-2xl">{product.name}</h2>
            <div className="flex gap-10 w-full mt-6">
              <div className="basis-1/2">
                <img className="w-full h-full object-contain mx-auto" src={`https://thumb.cloud.mail.ru/weblink/thumb/xw1/9Q7k/wEByutoNc/${productId}.jpg`} alt="" />
              </div>
              <div className="basis-1/2 flex flex-col gap-5">
                <div>
                  : 16; 19201200; IPS;<br/>
                  : Intel Core i7 12700H 2.3  (4.7 ,   Turbo)<br/>
                   : Intel Iris Xe graphics ;<br/>
                    16 , LPDDR4x;<br/>
                  : SSD 512 ;<br/>
                   : Windows 11 Home;<br/>
                  :   
                </div>
                <div>
                  <Price price={product.price} oldPrice={product.oldPrice} discount={product.discount} />
                </div>
                <div className="max-w-xs">
                  <ButtonBuy product={product} />
                </div>
              </div>
            </div>
          </div>
          <div className="max-w-3xl mx-auto flex flex-col gap-10">
            <div className="flex justify-between">
              <h3 className="text-3xl"> {product.reviews.length}</h3>
              <div>
                <SortOptions onSort={handleSortReviews} selectedSort={sortReviewBy} items={sortReviewsOptions} />
              </div>
              <div>
                <Pagination
                  pageSize={reviewOnPage}
                  onPageChange={handlePageReviewChange}
                  currentPage={currentPageReview}
                  itemsCount={reviews.length}
                />
              </div>
            </div>
            <ul className="flex flex-col gap-8 items-center">
              {reviewsCrop.map(review =>
                <li key={review.reviewId}>
                  <Review data={review} />
                </li>
              )}
            </ul>
            <div>
              <Pagination
                pageSize={reviewOnPage}
                onPageChange={handlePageReviewChange}
                currentPage={currentPageReview}
                itemsCount={reviews.length}
              />
            </div>
          </div>
        </div>
      );
    }
    return (
      <div className="flex justify-center text-3xl text-sky-500">
        <Loader />
      </div>
    );
  };

  useEffect(() => {
    setTimeout(() => {
      api.catalog.getById(productId)
        .then(response => {
          setProduct(response);
        });
    }, 2000);
  }, []);

  return (
    <div className="w-full max-w-screen-xl mx-auto px-8">
      {showProduct()}
    </div>
  );
};

export default ProductPage;
